import {useAccount, useConnect, useDisconnect} from "wagmi";
import {useEffect, useState} from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import {bsc} from "wagmi/chains";
import {formatWalletAddress} from "../../helpers/common";

const Header = () => {

    const { connect, connectors, error, isLoading, isSuccess, isError, pendingConnector } = useConnect({
        chainId: bsc.id,
        onSettled: (data, error) => {
            if(error) {
                //console.log(error)
                return;
            }
        }
    })

    const {disconnect} = useDisconnect();

    const { address, connector: wired, isConnected, isConnecting, isReconnecting } = useAccount()

    const [mounted, setMounted] = useState(false)
    const [loading, setLoading] = useState(false)
    const [connectModalShow, setConnectModalShow] = useState(true);

    const handleClose = () => setConnectModalShow(false);
    const handleShow = () => setConnectModalShow(true);

    useEffect(() => setMounted(true), []);

    useEffect(() => {
        if (typeof window === "undefined") {
            return;
        }
    },[mounted])

    useEffect(() => {
        if(isLoading || isConnecting || isReconnecting) {
            if(!isSuccess || !isError) {
                setLoading(true)
            } else {
                setLoading(false)
            }
        } else {
            setLoading(false)
        }
    }, [isLoading, isConnecting, isReconnecting, isSuccess, isError])

    useEffect(() => {
        if(isConnected && address) {
            setConnectModalShow(false)
        }
    }, [isConnected, address])

    useEffect(() => {
        if(error && error.message.length > 0) {
            console.log(error.message);
        }
    }, [error])


    return (
        <>
            <header className="header" id="header_main">
                <div className="container">
                    <div id="site-header-inner">
                        <div className="header__logo">
                            <a href="index.html"><img alt="" src="/images/logo/logo.png" /></a>
                        </div>
                        {!isConnected ? <a className="tf-button btn-effect" onClick={handleShow}>
                            <span className="boder-fade"></span>
                            <span className="effect">CONNECT WALLET</span>
                        </a> : <a className="tf-button btn-effect" onClick={disconnect}>
                            <span className="boder-fade"></span>
                            <span className="effect">DISCONNECT from {formatWalletAddress(address)}</span>
                        </a>}

                    </div>
                </div>
            </header>

            <Modal show={connectModalShow} onHide={handleClose} centered>
                <Modal.Header closeButton={true}>
                    <Modal.Title className="header-popup"><h4 className="text-black">Connect to a wallet</h4></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="box-wallet-inner">
                    {connectors.map((connector, i) => (
                        <button
                            key={i}
                            className="sc-box"
                            onClick={() => connector.ready && connect({ connector })}
                            disabled={isConnected || !connector.ready}
                        >
                            <>
                                {loading &&
                                connector.id === pendingConnector?.id ?
                                    <>
                                       {/* <Loader spinner={35} />*/}
                                        {loading &&
                                            connector.id === pendingConnector?.id &&
                                            ' (connecting)'}
                                    </> : <>
                                        <div className="img">
                                            <img src={`/images/connectors/${connector.id}.svg`} width={75} alt={connector.name} />
                                        </div>
                                        <h6 className="heading">
                                            {`${connector.name} ${isConnected && connector?.id === wired?.id ? "- Connected" : ""}`}
                                            {!connector.ready && ' (unsupported)'}
                                        </h6>
                                    </>
                                }
                            </>
                        </button>
                    ))}
                    </div>

                </Modal.Body>
                <Modal.Footer>
                    <p>Powered by CryptoWelts Inc</p>
                </Modal.Footer>
            </Modal>
        </>
    )
}

export default Header;