import {useEffect, useState} from "react";
import CurrencyInput from "../../helpers/currencyInput";
import {
    useAccount,
    useContractRead, useContractReads,
    useContractWrite,
    usePrepareContractWrite,
    useTransaction,
    useWaitForTransaction
} from "wagmi";
import { erc20ABI } from 'wagmi';
import {BUSDAddr, icoABI, icoAddr} from "../../dApp/contract";
import {bsc} from "wagmi/chains";
import {BigNumber, ethers} from "ethers";
import { Loading } from 'notiflix/build/notiflix-loading-aio';
import {Block, Report} from "notiflix";
import {parseEther} from "ethers/lib/utils";

const ICO = () => {

    const { address, isConnected } = useAccount();

    const [allowance, setAllowance] = useState(0);
    const [usdAmount, setUSDAmount] = useState(100);
    const [tokenAmount, setTokenAmount] = useState(0);
    const [amountValid, setAmountValid] = useState(false);
    const [soldTokens, setSoldTokens] = useState(0);
    const [userTokens, setUserTokens] = useState(0);
    const [latestTX, setLatestTX] = useState();

    const success = (t,m) => {
        Report.success(
            t || "SUCCESS!", m
        );
    }

    const failure = (e) => {
        Report.failure("OOPSS!", `Sorry, it seems that an error occurred. If the issue(s) still persist, 
        please let us know as soon as possible. ::More Info:: ${e.toString()} `, "Close", {
            messageMaxLength: 500
        })
    }

    const approveStatus = useContractRead({
        address: BUSDAddr,
        abi: erc20ABI,
        functionName: 'allowance',
        args: [address, icoAddr],
        enabled: isConnected && address,
        onSettled(data, error) {
            if(error) {
                failure(error?.message);
                return;
            }
            setAllowance(parseInt(data));
        }
    })

    const solds = useContractReads({
        contracts: [
            {
                address: icoAddr,
                abi: icoABI,
                functionName: 'TOTAL_SOLD',
            },
            {
                address: icoAddr,
                abi: icoABI,
                functionName: 'getBuyer',
                args:[address]
            },
        ],
        watch: true,
        onSettled(data, error) {

            if(error) return;

            if(data && data.length > 1) {
                setSoldTokens(parseFloat((data[0] / 10**18 || 0).toString()).toFixed(5))
                setUserTokens(parseFloat((data[1]?.totalBought / 10**18 || 0).toString()).toFixed(5))
            }

        }
    })

    const approveConfig = usePrepareContractWrite({
        address: BUSDAddr,
        abi: erc20ABI,
        functionName: 'approve',
        args: [icoAddr, ethers.utils.parseEther(parseFloat(`${usdAmount || 100}`).toFixed(2))],
        chainId: bsc.id,
        enabled: isConnected && address,
        onSettled(data, error) {
            if(error) {
                failure(error?.message);
                return;
            }
        }
    })

    const buyConfig = usePrepareContractWrite({
        address: '0x006C553772336C420eCb06124857DFaF2388f43C',
        abi: icoABI,
        functionName: 'buy',
        args: [amountValid],
        chainId: bsc.id,
        enabled: allowance > 0 && amountValid
    })

    const { data: approveData, write: approve, error: approveError } = useContractWrite({...approveConfig.config,
        onSuccess(data) {
            setLatestTX(data.hash)
        }
    })

    const { data: buyData, write: buy, error: buyError } = useContractWrite({...buyConfig.config,
        onSettled(data,error) {
            if(error) {
                failure(error?.message);
                return;
            }
            setLatestTX(data.hash);
        }
    })

    const waitForTransaction = useWaitForTransaction({
        hash: latestTX,
        onSettled(data, error) {

            if(error) {
                failure(error?.message || approveError?.message || buyError?.message || "---");
                return;
            }

            if(approveData?.hash === data.transactionHash) {
                success(`APPROVED!`, `You've successfully approved and granted access our ICO smart contract for 
                let it spending your allowance in BUSD from your wallet.`)
            }

            if(buyData?.hash === data.transactionHash) {
                success("PURCHASED!", `You've successfully placed your order
                        (${parseFloat(data?.logs[0]?.data / 0.03).toFixed(3)})
                        in exchange for ${ data?.logs[0]?.data } BUSD.`)
            }

        }
    })

    const {data: txData, error: txError, refetch: txQuery} = useTransaction({
        hash: latestTX,
        enabled: false,
        onError(error) {
            console.log('Error', error)
        },
    })

    const handleBuyAmount = (e) => {
        const val = !e.target.value ? null : parseFloat(e.target.value.toString());
        setUSDAmount(val);
    }

    useEffect(() => {

        if(approveStatus.isLoading || approveStatus.fetchStatus === "fetching") {
            Block.hourglass('.content-about', "Updating...", {
                backgroundColor: 'rgba(0,0,0,0.35)', messageColor: '#fff', svgSize:'155px', messageFontSize: '20px'
            });
        } else {
            Block.remove(".content-about", 3500)
        }

    }, [approveStatus])

    useEffect(() => {
        if(allowance) {
            approveStatus.refetch?.()
        }
    }, [allowance])


    useEffect(() => {
        if(waitForTransaction.isLoading || waitForTransaction.fetchStatus === "fetching") {
            Loading.hourglass("Your request has been submitted successfully and it is still being " +
                "processed... Please wait a while until the transaction has been confirmed by the network..", {
                messageMaxLength:500
            });
        } else {
            Loading.remove(1500)
        }
    }, [waitForTransaction])

    useEffect(() => {
        setAmountValid(false)
        setTokenAmount("Minimum Buy: 100 BUSD")
        if(usdAmount >= 100) {
            setAmountValid(true)
            setTokenAmount(`~ ${parseFloat((usdAmount / 0.03).toString()).toFixed(2)} MTP`)
        }
    }, [usdAmount])

    return (
        <section className="tf-section tf-about pb-mobie mt-5" id="mesopotamians">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12 col-lg-5">
                        <div className="content-about mobie-40" data-aos="fade-up" data-aos-duration="800">
                            <div className="tf-title st2">
                                <p className="h8 sub-title"></p>
                                <h4 className="title">Join ICO!</h4>
                            </div>
                            <p>After typing how many MTPs you will buy in the box below, click the buy button.</p>
                            <span className="boder-fade"></span>
                            <CurrencyInput placeholder={"100 BUSD"} value={usdAmount} className={amountValid ? "border-success" : "border-danger"} onChange={e => handleBuyAmount(e)} type="text" />
                            <input type="text" value={tokenAmount} readOnly={true} />
                            <button className="tf-button btn-effect" disabled={!amountValid || !approve} onClick={() => approve?.()}>
                                <span className="boder-fade"></span>

                                <span className="effect">Approve</span>
                            </button>
                            <button className="tf-button btn-effect ml-3" disabled={!amountValid || !buy} onClick={() => buy?.()}>
                                <span className="boder-fade"></span>

                                <span className="effect">Buy MTP</span>
                            </button>
                            <br />
                            <h4 className="title mt-5">Yours: {userTokens} MTP</h4>
                            <h4 className="title mt-5">Total Sold: {soldTokens} MTP</h4>
                        </div>
                    </div>
                    <div className="col-sm-12 col-lg-7 mt-sm-5">
                        <div className="wrap-about p-l70" data-aos="fade-up" data-aos-duration="800">
                            <div className="box-text corner-box active">
                                <div className="h7">Presale Price</div>
                                <p> $0.03</p>
                            </div>
                            <div className="box-text corner-box">
                                <div className="h7">Presale Token Amount</div>
                                <p>6.000.000 MTP</p>
                            </div>
                            <div className="box-text corner-box mb-0">
                                <div className="h7">Vesting</div>
                                <p>1 month cliff, then 3 months vesting</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ICO;